  get listFilter() {
    return this.stateTag === '' ? this.listArticle
      : this.listArticle.filter((d) => d.hashtags.indexOf(this.stateTag) >= 0);
  }

  get sliceList() {
    return this.listFilter.slice((this.pageIdx - 1) * this.pageSplitNum, this.pageIdx * this.pageSplitNum);
  }

  get pages() {
    return Math.ceil(this.listFilter.length / this.pageSplitNum);
  }

  show() {
